* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: gainsboro;
  height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  width: 100%;
  flex-wrap: wrap;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-bottom: 4rem;
  margin-top: 100px;
}

.movies a {
  display: contents;
  text-decoration: none;
}

@media screen and (max-width: 1090px) {
  .movies {
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
  }
}
