* {
  box-sizing: border-box;
}

body {
  padding: 10px;
}

.detail__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 10px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  width: 90%;
  display: flex;
  overflow: hidden;
}

.detail__image {
  width: 100%;
  height: 100%;
  max-width: 20rem;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(0, 0, 0, 0.3);
}

.detail__data__container {
  width: 80%;
}

.detail__info__container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
}

.detail__title,
.detail__year {
  margin: 0;
}

.detail__title {
  margin-bottom: 10px;
  font-size: 4rem;
  font-weight: 600;
  color: #3b3b3b;
}

.detail__year,
.detail__genres {
  opacity: 0.5;
  font-weight: 300;
  font-size: 1.2rem;
}

.detail__genres {
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  margin-top: 10px;
  margin-left: 10px;
  font-style: italic;
  color: seagreen;
}

.detail__genres__genre {
  margin-right: 1rem;
}

.detail__summary {
  margin-top: 40px;
  font-weight: 300;
  font-size: 1.5rem;
  line-height: 2.3rem;
}

@media screen and (max-width: 1090px) {
  .detail__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 10px;
    border-radius: 10px;
    background-color: white;
    margin: 0 auto;
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  .detail__image {
    width: 70%;
    height: 70%;
    max-width: 20rem;
    margin: 0;
    margin-top: 1rem;
    box-shadow: 0 13px 27px -5px rgba(0, 0, 0, 0.3);
  }

  .detail__title {
    margin-bottom: 10px;
    font-size: 2.2rem;
    font-weight: 600;
    color: #3b3b3b;
    margin-top: 1rem;
  }

  .detail__info__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: baseline;
  }
  .detail__genres {
    list-style: none;
    display: flex;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    margin-top: 10px;
    font-style: normal;
    color: seagreen;
  }

  .detail__summary {
    margin-top: 1rem;
  }
}
