.movies .movie {
  background-color: whitesmoke;
  margin-bottom: 2.5rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  color: #525252;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__data {
  width: 100%;
  overflow: hidden;
}

.movie__image {
  width: 100%;
  max-width: 10rem;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(0, 0, 0, 0.3);
}

.movie__title,
.movie__year {
  margin: 0;
}

.movie__title {
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  color: #3b3b3b;
}

.movie__year,
.movie__genres {
  opacity: 0.5;
  font-weight: 400;
  font-size: 0.7rem;
}

.movie__genres {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  margin-top: 4px;
  font-style: italic;
  color: seagreen;
}

.genres__genre {
  margin-right: 8px;
}

.movie__summary {
  font-size: 0.9rem;
}
