.nav {
  z-index: 1;
  position: sticky;
  top: 20px;
  display: flex;
  align-items: center;
  background-color: white;
  padding: 10px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 10px;
}

@media screen and (max-width: 1090px) {
  .nav {
    position: fixed;
    display: flex;
    justify-content: center;
    left: initial;
    top: initial;
    bottom: 0px;
    width: 100%;
    border-radius: 0px;
  }
}

.nav a {
  text-decoration: none;
  color: seagreen;
  font-size: 2rem;
  font-weight: 600;
  margin: 0px 20px;
}
